<template>
  <div class="loginDiv">
    <h1>Clone Tiktok</h1>
    <input v-model="account" type="text" placeholder="account..." />
    <input v-model="password" type="password" placeholder="password..." />
    <button @click="Login">Login</button>
    <button @click="test">test</button>t
  </div>
</template>

<script>
import sywekAxios from "../src/reference/axiosMsgReaction";
export default {
  name: "login",
  data() {
    return {
      account: "",
      password: "",
    };
  },
  methods: {
    async test() {
      let _data = await sywekAxios.get(process.env.VUE_APP_API_URL + "/auth", { params: { count: 3, offset: 0 } }, true);
      console.log(process.env.VUE_APP_API_URL, _data);
    },
    async Login() {
      let _data = await sywekAxios.post(process.env.VUE_APP_API_URL + "/auth", { account: this.account, password: this.password }, {}, true);

      console.log(process.env.VUE_APP_API_URL, _data);
    },
  },
};
</script>

<style scoped>
h1 {
  color: blueviolet;
  margin-top: 3rem;
}
.loginDiv {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #333;
}

input {
  width: 80%;
  display: block;
  font-size: 1.5rem;
  margin: 3rem auto;
  border-radius: 0.5rem;
  background-color: #222;
  color: wheat;
}

button {
  width: 60%;
  font-size: 1.2rem;
  background-color: #777;
  color: seashell;
  border-radius: 1rem;
}
</style>
